import React, { useState } from "react";
import {
  Tooltip,
  Fab,
  makeStyles,
  Modal,
  Container,
  TextField,
  MenuItem,
  Button,
  Typography,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
  Snackbar,
} from "@material-ui/core";
import { Add as AddIcon } from "@material-ui/icons";
import MuiAlert from "@material-ui/lab/Alert";
import { useDispatch } from "react-redux";
import { createStudent } from "../../redux/action/student";
const useStyles = makeStyles((theme) => ({
  fab: {
    position: "fixed",
    bottom: 20,
    left: 20,
  },
  title: {
    fontFamily: "BYekan",
    color: "#555",
  },
  container: {
    width: 500,
    height: "auto",
    backgroundColor: "white",
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    margin: "auto",
    borderRadius: 10,
    [theme.breakpoints.down("sm")]: {
      width: "100vw",
      height: "100vh",
    },
  },
  form: {
    padding: theme.spacing(2),
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
  },
  item: {
    marginBottom: theme.spacing(2),
    width: "100%",
    display: "flex",
    justifyContent: "center",
    fontFamily: "BYekan",
  },
}));

const Alert = (props) => {
  return <MuiAlert evelotion={6} variant="filled" {...props} />;
};

function Add() {
  const classess = useStyles();
  const dispatch = useDispatch();
  const [form, setForm] = useState({
    name: "",
    family: "",
    fatherName: "",
    primaryPhoneNumber: "",
    secondaryPhoneNumber: "",
    address: "",
    educationalDegree: "",
    favoriteField: "",
    individualSkills: "",
  });

  const [open, setOpen] = useState(false);
  const [openAlert, setOpenAlert] = useState(false);

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
  };

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(createStudent(form));
    setOpen(false);
  };

  return (
    <>
      <Tooltip title="Add" aria-label="add" onClick={() => setOpen(true)}>
        <Fab color="primary" className={classess.fab}>
          <AddIcon />
        </Fab>
      </Tooltip>
      <Modal open={open}>
        <Container className={classess.container}>
          <form
            className={classess.form}
            autoComplete="off"
            onSubmit={handleSubmit}
          >
            <Typography variant="h5" className={classess.title}>
                 
            </Typography>
            <div className={classess.item}>
              <TextField
                id="standard-basic"
                name="name"
                label=""
                onChange={handleChange}
                size="small"
                style={{ width: "100%" }}
              />
            </div>
            <div className={classess.item}>
              <TextField
                id="standard-basic"
                label=" "
                name="family"
                size="small"
                onChange={handleChange}
                style={{ width: "100%" }}
              />
            </div>
            <div className={classess.item}>
              <TextField
                id="standard-basic"
                onChange={handleChange}
                label=" "
                name="fatherName"
                size="small"
                style={{ width: "100%" }}
              />
            </div>
            <div className={classess.item}>
              <TextField
                onChange={handleChange}
                id="standard-basic"
                name="primaryPhoneNumber"
                label=" "
                size="small"
                style={{ width: "100%" }}
              />
            </div>
            <div className={classess.item}>
              <TextField
                id="standard-basic"
                label=" "
                onChange={handleChange}
                name="secondaryPhoneNumber"
                size="small"
                style={{ width: "100%" }}
              />
            </div>
            <div className={classess.item}>
              <TextField
                id="standard-basic"
                label=""
                name="address"
                size="small"
                onChange={handleChange}
                style={{ width: "100%" }}
              />
            </div>
            <div className={classess.item}>
              <TextField
                id="standard-basic"
                name="educationalDegree"
                label=" "
                size="small"
                onChange={handleChange}
                style={{ width: "100%" }}
              />
            </div>
            <div className={classess.item}>
              <TextField
                id="standard-basic"
                name="favoriteField"
                label="  "
                size="small"
                style={{ width: "100%" }}
                onChange={handleChange}
              />
            </div>
            <div className={classess.item}>
              <TextField
                id="standard-basic"
                name="individualSkills"
                label=" "
                size="small"
                onChange={handleChange}
                style={{ width: "100%" }}
              />
            </div>
            <div className={classess.item}>
              <Button
                type="submit"
                variant="outlined"
                color="primary"
                style={{ marginLeft: 20 }}
              >
                {" "}
              </Button>
              <Button
                variant="contained"
                color="secondary"
                onClick={() => setOpen(false)}
              >
                 {" "}
              </Button>
            </div>
          </form>
        </Container>
      </Modal>
      <Snackbar
        open={openAlert}
        autoHideDuration={3000}
        onClose={handleClose}
        anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
      >
        <Alert>This is a success message</Alert>
      </Snackbar>
    </>
  );
}

export default Add;
