import * as api from "../../service/api";

export const getAllStudent = (page) => async (dispatch) => {
  dispatch({ type: "REQUEST_ALL_STUDENTS" });
  try {
    const { data } = await api.fetchStudents(page);
    dispatch({ type: "ALL_STUDENTS_DONE", payload: data?.data });
  } catch (error) {
    dispatch({ type: "ALL_STUDENTS_FAIL" });
  }
};

export const removeStudent = (userId) => async (dispatch) => {
  dispatch({ type: "REQUEST_DELET_STUDENT" });
  try {
    await api.deleteStudent(userId);
    dispatch({ type: "DELETE_STUDENT_DONE", payload: userId });
  } catch (error) {
    dispatch({ type: "DELETE_STUDENT_FAIL", payload: error.response });
  }
};

// add student
export const createStudent = (formData) => async (dispatch) => {
  dispatch({ type: "REQUEST_ADD_STUDENT" });
  try {
    const { data } = await api.addStudent(formData);
    const result = data?.data?.result;
    dispatch({ type: "ADD_STUDENT_DONE", payload: result });

    alert("create student successfully");
  } catch (error) {
    dispatch({ type: "ADD_STUDENT_FAIL", payload: error.responsee });
  }
};

// get detail student

export const detailStudent = (userId) => async (dispatch) => {
  dispatch({ type: "REQUEST_DETAIL_STUDENT" });
  try {
    const { data } = await api.getStudentDetail(userId);
    const result = data?.data?.result;
    dispatch({ type: "DETAIL_STUDENT_DONE", payload: result });
  } catch (error) {
    dispatch({ type: "DETAIL_STUDENT_FAIL" });
  }
};

// get a student for update

export const getStudent = (userId) => ({
  type: "GET_STUDENT_UPLOAD",
  payload: userId,
});

// update a student
export const updatedStudent = (formData, userId) => async (dispatch) => {
  dispatch({ type: "REQUEST_UPDATE_STUDENT" });
  try {
    const { data } = await api.updateStudent(formData);
    const result = data?.data?.result;
    dispatch({ type: "UPDATE_STUDENT_DONE", payload: { userId, result } });
    alert("updated student successfully");
  } catch (error) {
    dispatch({ type: "UPDATE_STUDENT_FAIL", payload: error.response });
  }
};
