import React, { useState } from "react";
import {
  Container,
  makeStyles,
  Menu,
  Button,
  MenuItem,
} from "@material-ui/core";
import { Table, Thead, Tbody, Tr, Th, Td } from "react-super-responsive-table";
import { Delete, Edit, MoreVert } from "@material-ui/icons";
import "react-super-responsive-table/dist/SuperResponsiveTableStyle.css";
import { useDispatch, useSelector } from "react-redux";
import { getStudent, removeStudent } from "../../redux/action/student";
import { Link } from "react-router-dom";
import UpdateDialog from "../Dialog/UpdateStudent";

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(5),
    // textAlign: "center",
  },
  table: {
    border: "1px solid #ECE7E7",
    padding: theme.spacing(1, 2),
    backgroundColor: "white",
  },
  tr: {
    border: "1px solid #ECE7E7",
  },
  th: {
    padding: theme.spacing(3, 0),
    borderBottom: "1px solid #e7e7e7",
    fontSize: 16,
    color: "#555",
  },
  mentItem: {
    display: "flex",
    justifyContent: "space-between",
    fontFamily: "BYekan",
    width: "130px",
  },
}));

const DataTable = ({ students }) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [open, setOpen] = useState(false);
  const classess = useStyles();
  const dispatch = useDispatch();
  const search = useSelector((state) => state.search);
  const { searchTerm } = search;
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  //   const data = students?.data;
  return (
    <Container className={classess.container}>
      <Table className={classess.table}>
        <Thead>
          <Tr className={classess.tr}>
            <Th className={classess.th}> </Th>
            <Th className={classess.th}> </Th>
            <Th className={classess.th}> </Th>
            <Th className={classess.th}> </Th>
            <Th className={classess.th}> </Th>
            <Th className={classess.th}></Th>
            <Th className={classess.th}></Th>
            <Th className={classess.th}></Th>
          </Tr>
        </Thead>
        <Tbody>
          {students ? (
            students?.map((item) => (
              <Tr key={item.userId}>
                <Td className={classess.th}>{item?.name}</Td>
                <Td className={classess.th}>{item?.family}</Td>
                <Td className={classess.th}>{item?.fatherName}</Td>
                <Td className={classess.th}>{item?.primaryPhoneNumber}</Td>
                <Td className={classess.th}>{item?.educationalDegree}</Td>
                <Td className={classess.th}>
                  <Link to={`/list-student/${item?.userId}`}>
                    <Button variant="contained" color="primary">
                      
                    </Button>
                  </Link>
                </Td>

                <Td style={{ padding: "16px", color: "#6A676B" }}>
                  <Button
                    onClick={() => {
                      setOpen(true);
                      dispatch(getStudent(item.userId));
                    }}
                    variant="contained"
                    style={{ backgroundColor: "#00e676", color: "#fff" }}
                  >
                    
                  </Button>
                </Td>
                <Td style={{ padding: "16px", color: "#6A676B" }}>
                  <Button
                    onClick={() => dispatch(removeStudent(item?.userId))}
                    variant="contained"
                    color="secondary"
                  >
                    
                  </Button>
                </Td>
                <Td className={classess.th}>
                  {/* <Button
                    aria-controls="simple-menu"
                    aria-haspopup="true"
                    onClick={handleClick}
                  >
                    <MoreVert />
                  </Button>
                  <Menu
                    id="simple-menu"
                    anchorEl={anchorEl}
                    keepMounted
                    open={Boolean(anchorEl)}
                    onClose={handleClose}
                  >
                    <MenuItem
                      onClick={() => {
                        console.log(item.userId);
                        // dispatch(removeStudent(item?.userId));
                        // handleClose();
                        // console.log("delete item", item.userId);
                      }}
                      className={classess.mentItem}
                    >
                       <Delete color="secondary" />
                    </MenuItem>
                    <MenuItem
                      onClick={() => {
                        setOpen(true);
                        dispatch(getStudent(item.userId));
                      }}
                      className={classess.mentItem}
                    >
                      
                      <Edit color="primary" />
                    </MenuItem>
                  </Menu> */}
                </Td>
                <UpdateDialog
                  handleClose={handleClose}
                  open={open}
                  setOpen={setOpen}
                />
              </Tr>
            ))
          ) : (
            <h1>Loading...</h1>
          )}
        </Tbody>
      </Table>
    </Container>
  );
};
export default DataTable;
