import React, { useEffect, useState } from "react";

import {
  makeStyles,
  Modal,
  Container,
  TextField,
  Button,
  Typography,
  FormLabel,
} from "@material-ui/core";
import { useDispatch } from "react-redux";
import { updatedStudent } from "../../redux/action/student";
import { useSelector } from "react-redux";
const useStyles = makeStyles((theme) => ({
  fab: {
    position: "fixed",
    bottom: 20,
    left: 20,
  },
  title: {
    fontFamily: "BYekan",
    color: "#555",
  },
  container: {
    width: 500,
    height: "auto",
    backgroundColor: "white",
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    margin: "auto",
    borderRadius: 10,
    [theme.breakpoints.down("sm")]: {
      width: "100vw",
      height: "100vh",
    },
  },
  form: {
    padding: theme.spacing(2),
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
  },
  item: {
    marginBottom: theme.spacing(2),
    width: "100%",
    display: "flex",
    justifyContent: "center",
    fontFamily: "BYekan",
  },
}));

const UpdateDialog = ({ open, setOpen, handleClose }) => {
  const classess = useStyles();
  const student = useSelector((state) => state.student?.studentDetail);
  const userId = student?.userId;
  console.log("student details", student);
  const dispatch = useDispatch();
  const [form, setForm] = useState({
    name: student?.name,
    family: student?.family,
    fatherName: student?.fatherName,
    primaryPhoneNumber: student?.primaryPhoneNumber,
    secondaryPhoneNumber: student?.secondaryPhoneNumber,
    address: student?.address,
    educationalDegree: student?.educationalDegree,
    favoriteField: student?.favoriteField,
    individualSkills: student?.individualSkills,
    userId: student?.userId,
  });

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };
  const handleSubmit = (e) => {
    e.preventDefault();

    dispatch(updatedStudent({ ...form, userId }, userId));
    setOpen(false);
    handleClose();
  };

  return (
    <Modal open={open}>
      <Container className={classess.container}>
        <form
          className={classess.form}
          autoComplete="off"
          onSubmit={handleSubmit}
        >
          <Typography variant="h5" className={classess.title}>
               {student?.name}
          </Typography>
          <div className={classess.item}>
            <TextField
              id="standard-basic"
              name="name"
              label=""
              onChange={handleChange}
              size="small"
              defaultValue={student?.name}
              style={{ width: "100%" }}
            />
          </div>
          <div className={classess.item}>
            <TextField
              id="standard-basic"
              label=" "
              defaultValue={student?.family}
              name="family"
              size="small"
              onChange={handleChange}
              style={{ width: "100%" }}
            />
          </div>
          <div className={classess.item}>
            <TextField
              id="standard-basic"
              onChange={handleChange}
              label=" "
              defaultValue={student?.fatherName}
              name="fatherName"
              size="small"
              style={{ width: "100%" }}
            />
          </div>
          <div className={classess.item}>
            <TextField
              onChange={handleChange}
              id="standard-basic"
              name="primaryPhoneNumber"
              defaultValue={student?.primaryPhoneNumber}
              label=" "
              size="small"
              style={{ width: "100%" }}
            />
          </div>
          <div className={classess.item}>
            <TextField
              id="standard-basic"
              label=" "
              defaultValue={student?.secondaryPhoneNumber}
              onChange={handleChange}
              name="secondaryPhoneNumber"
              size="small"
              style={{ width: "100%" }}
            />
          </div>
          <div className={classess.item}>
            <TextField
              id="standard-basic"
              label=""
              defaultValue={student?.address}
              name="address"
              size="small"
              onChange={handleChange}
              style={{ width: "100%" }}
            />
          </div>
          <div className={classess?.item}>
            <TextField
              id="standard-basic"
              name="educationalDegree"
              label=" "
              defaultValue={student?.educationalDegree}
              size="small"
              onChange={handleChange}
              style={{ width: "100%" }}
            />
          </div>
          <div className={classess.item}>
            <TextField
              id="standard-basic"
              name="favoriteField"
              label="  "
              defaultValue={student?.favoriteField}
              size="small"
              style={{ width: "100%" }}
              onChange={handleChange}
            />
          </div>
          <div className={classess.item}>
            <TextField
              id="standard-basic"
              name="individualSkills"
              label=" "
              defaultValue={student?.individualSkills}
              size="small"
              onChange={handleChange}
              style={{ width: "100%" }}
            />
          </div>
          <div className={classess.item}>
            <Button
              type="submit"
              variant="outlined"
              color="primary"
              style={{ marginLeft: 20 }}
            >
              {" "}
            </Button>
            <Button
              variant="contained"
              color="secondary"
              onClick={() => {
                setOpen(false);
                handleClose();
              }}
            >
               {" "}
            </Button>
          </div>
        </form>
      </Container>
    </Modal>
  );
};
export default UpdateDialog;
