export const studentReducer = (
  state = {
    allStudents: [],
    studentDetail: {},
  },
  action
) => {
  switch (action.type) {
    //   get all students
    case "REQUEST_ALL_STUDENTS":
      return {
        ...state,
      };
    case "ALL_STUDENTS_DONE":
      return {
        ...state,
        allStudents: action.payload?.result?.data,
        totalItem: action.payload?.result?.totalItem,
      };
    case "ALL_STUDENTS_FAIL":
      return {
        error: action.payload,
      };
    //   delete a student
    case "REQUEST_DELET_STUDENT":
      return {
        ...state,
      };
    case "DELETE_STUDENT_DONE":
      return {
        ...state,
        success: true,
        allStudents: state.allStudents.filter(
          (item) => item.userId !== action.payload
        ),
      };
    case "DELETE_STUDENT_FAIL":
      return {
        error: action.payload,
      };
    //   add student
    case "REQUEST_ADD_STUDENT":
      return { ...state };

    case "ADD_STUDENT_DONE":
      return {
        ...state,
        success: true,
        allStudents: [...state?.allStudents, action.payload],
      };

    case "ADD_STUDENT_FAIL":
      return {
        error: action.payload,
      };

    //   get student detail
    case "REQUEST_DETAIL_STUDENT":
      return {
        loading: true,
      };
    case "DETAIL_STUDENT_DONE":
      return {
        studentDetail: action.payload,
      };
    case "DETAIL_STUDENT_FAIL":
      return {
        error: action.payload,
      };

    // get a student for updating

    case "GET_STUDENT_UPLOAD":
      return {
        ...state,
        studentDetail: state.allStudents.find(
          (item) => item.userId === action.payload
        ),
      };

    //   update a student
    case "REQUEST_UPDATE_STUDENT":
      return {
        ...state,
      };
    case "UPDATE_STUDENT_DONE": {
      return {
        ...state,
        allStudents: state.allStudents.map((item) =>
          item.userId === action.payload?.userId ? action.payload.result : item
        ),
      };
    }
    case "UPDATE_STUDENT_FAIL":
      return {
        error: action.payload,
      };
    default:
      return state;
  }
};
