import { makeStyles } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Table } from "../components";
import Loading from "../components/Loading/Loading";
import Message from "../components/Message/Message";
import { getAllStudent } from "../redux/action/student";
import Paginate from "../utils/Pagination";

const useStyles = makeStyles((theme) => ({
  studentContainer: {
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
}));

const Student = () => {
  const [page, setPage] = useState(1);
  const classess = useStyles();
  const dispatch = useDispatch();
  const students = useSelector((state) => state.student);
  const { loading, error, allStudents, totalItem } = students;
  // const countPaginate = Math.floor(totalItem / allStudents.length);
  console.log("students", students);
  useEffect(() => {
    dispatch(getAllStudent(page));
  }, [dispatch, page]);

  if (error) {
    <Message type="error" message={error} />;
  }

  if (loading) {
    return <Loading />;
  }

  return (
    <div className={classess.studentContainer}>
      {allStudents && <Table students={allStudents} />}
      {totalItem > 5 && (
        <Paginate setPage={setPage} page={page} totalItem={totalItem} />
      )}
    </div>
  );
};

export default Student;
