import { CssBaseline } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { Navigate, Route, Routes } from "react-router-dom";
import { Feed } from "./components";
import MainLayout from "./Layout/MainLayout";
import Home from "./screen/Home";
import SingIn from "./screen/SingIn";
import Student from "./screen/Student";
import StudentDetail from "./screen/StudentDetail";

const App = () => {
  return (
    <div>
      <MainLayout>
        <CssBaseline />
        <Routes>
          <Route path="/" element={<SingIn />} />
          <Route path="/list-student" element={<Student />} />
          <Route path="/list-student/:userId" element={<StudentDetail />} />
          {/* <Route path="/login" element={<SingIn />} /> */}
        </Routes>
      </MainLayout>
    </div>
  );
};
export default App;
