export const authReducer = (state = { authData: {} }, action) => {
  switch (action.type) {
    //   LOGIN REQUEST
    case "REQUEST_LOGIN":
      return {
        loading: true,
      };
    case "LOGIN_DONE":
      return {
        loading: false,
        authData: action.payload,
      };
    case "LOGIN_FAIL":
      return {
        loading: false,
        error: action.payload,
      };
    // LOGOUT REQUEST
    case "LOGOUT_DONE":
      return {
        authData: {},
      };
    case "LOGOUT_FAIL":
      return {
        error: action.payload,
      };
    default:
      return state;
  }
};
