import React from "react";
import {
  Avatar,
  Container,
  ImageList,
  ImageListItem,
  makeStyles,
  Typography,
  Link,
  Divider,
} from "@material-ui/core";
import { AvatarGroup } from "@material-ui/lab";
import picProfile from "../../assets/img/mahmood.jpg";

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(5),
    position: "sticky",
    top: 0,
  },
  title: {
    fontSize: 18,
    fontWeight: 700,
    color: "#555",
    fontFamily: "BYekan",
  },
  imageList: {},
  link: {
    marginRight: theme.spacing(2),
    color: "#555",
    fontWeight: "bold",
    textDecoration: "none",
  },
  wrapper: {
    display: "flex",
    flexWrap: "wrap",
  },
  linkItem: {
    fontSize: 14,
    fontWeight: 700,
    fontFamily: "BYekan",
  },
}));

function Rightbar() {
  const classess = useStyles();
  return (
    <Container className={classess.container}>
      <Typography gutterBottom variant="h6" className={classess.title}>
         
      </Typography>
      <AvatarGroup max={4} style={{ marginBottom: 20 }}>
        <Avatar alt="mahmoud" src={picProfile} />
        <Avatar alt="mahmoud" src={picProfile} />
        <Avatar alt="mahmoud" src={picProfile} />
        <Avatar alt="mahmoud" src={picProfile} />
        <Avatar alt="mahmoud" src={picProfile} />
        <Avatar alt="mahmoud" src={picProfile} />
      </AvatarGroup>
      <Typography gutterBottom variant="h6" className={classess.title}>
        
      </Typography>
      <ImageList
        rowHeight={100}
        className={classess.imageList}
        cols={3}
        style={{ marginBottom: 20 }}
      >
        <ImageListItem>
          <img src={picProfile} alt="mahmoud" />
        </ImageListItem>
        <ImageListItem>
          <img src={picProfile} alt="mahmoud" />
        </ImageListItem>
        <ImageListItem>
          <img src={picProfile} alt="mahmoud" />
        </ImageListItem>
        <ImageListItem>
          <img src={picProfile} alt="mahmoud" />
        </ImageListItem>
      </ImageList>
      <Typography gutterBottom variant="h6" className={classess.title}>
         
      </Typography>

      <div className={classess.wrapper}>
        <Link href="#" className={classess.link} variant="body2">
          <Typography
            gutterBottom
            variant="body1"
            className={classess.linkItem}
          >
            
          </Typography>
        </Link>
        <Link href="#" className={classess.link} variant="body2">
          <Typography
            gutterBottom
            variant="body1"
            className={classess.linkItem}
          >
            
          </Typography>
        </Link>

        <Link href="#" className={classess.link} variant="body2">
          <Typography
            gutterBottom
            variant="body1"
            className={classess.linkItem}
          >
             
          </Typography>
        </Link>
        <Link href="#" className={classess.link} variant="body2">
          <Typography
            gutterBottom
            variant="body1"
            className={classess.linkItem}
          >
            
          </Typography>
        </Link>
        <Divider flexItem style={{ marginBottom: 10 }} />
        <Link href="#" className={classess.link} variant="body2">
          <Typography
            gutterBottom
            variant="body1"
            className={classess.linkItem}
          >
             
          </Typography>
        </Link>
        <Link href="#" className={classess.link} variant="body2">
          <Typography
            gutterBottom
            variant="body1"
            className={classess.linkItem}
          >
             
          </Typography>
        </Link>
        <Link href="#" className={classess.link} variant="body2">
          <Typography
            gutterBottom
            variant="body1"
            className={classess.linkItem}
          >
            
          </Typography>
        </Link>
      </div>
    </Container>
  );
}

export default Rightbar;
