import axios from "axios";

const API = axios.create({ baseURL: "http://194.59.170.107/api" });

API.interceptors.request.use(async (config) => {
  const userData = localStorage.getItem("userInfo")
    ? JSON.parse(localStorage.getItem("userInfo"))
    : null;
  const token = userData?.data?.result?.accessToken;
  if (token) {
    config.headers["access-token"] = token;
  }
  return config;
});
// sing in api
export const singIn = (formData) => API.put("/login", formData);
// logout api
export const logout = () => API.get("/logout");
// get all strudent api
export const fetchStudents = (page) => API.get(`/students?page=${page}`);
// delete student api
export const deleteStudent = (userId) =>
  API.delete(`/student?userId=${userId}`);
// add studebt api
export const addStudent = (formData) => API.post("/student", formData);
// get a student
export const getStudentDetail = (userId) =>
  API.get(`/student?userId=${userId}`);
// update a student
export const updateStudent = (updatedData) => API.put("/student", updatedData);
