import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import { red } from "@material-ui/core/colors";
import FavoriteIcon from "@material-ui/icons/Favorite";
import ShareIcon from "@material-ui/icons/Share";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import { Container } from "@material-ui/core";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { detailStudent } from "../redux/action/student";
import Loading from "../components/Loading/Loading";
import bg from "../assets/img/pexels-cottonbro-6804581.jpg";
const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(5),
  },
  root: {
    maxWidth: "100%",
  },
  title: {
    fontFamily: "BYekan",
    marginRight: theme.spacing(1),
    color: "#555",
  },
  media: {
    height: 0,
    paddingTop: "56.25%", // 16:9
    fontFamily: "BYekan",
  },
  expand: {
    transform: "rotate(0deg)",
    marginRight: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  avatar: {
    backgroundColor: red[500],
  },
}));

const StudentDetail = () => {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const { userId } = useParams();
  const dispatch = useDispatch();
  const detail = useSelector((state) => state.student);
  const { loading, studentDetail } = detail;

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  useEffect(() => {
    dispatch(detailStudent(userId));
  }, [dispatch]);

  if (loading) {
    return <Loading />;
  }

  return (
    <Container className={classes.container}>
      <Card className={classes.root}>
        <CardHeader
          avatar={
            <Avatar aria-label="recipe" className={classes.avatar}>
              R
            </Avatar>
          }
          //   action={
          //     <IconButton aria-label="settings">
          //       <MoreVertIcon />
          //     </IconButton>
          //   }
          title={
            <Typography className={classes.title}>
              {studentDetail?.name + " " + studentDetail?.family}
            </Typography>
          }
          subheader={
            <Typography className={classes.title}>
              {studentDetail?.favoriteField}
            </Typography>
          }
        />
        {/* <CardMedia className={classes.media} image={bg} title="Paella dish" /> */}
        <CardContent>
          <Typography
            className={classes.title}
            variant="body2"
            color="textSecondary"
            component="p"
          >
                  
          </Typography>
        </CardContent>
        <CardActions disableSpacing>
          <IconButton aria-label="add to favorites">
            <FavoriteIcon />
          </IconButton>
          <IconButton aria-label="share">
            <ShareIcon />
          </IconButton>
          <IconButton
            className={clsx(classes.expand, {
              [classes.expandOpen]: expanded,
            })}
            onClick={handleExpandClick}
            aria-expanded={expanded}
            aria-label="show more"
          >
            <ExpandMoreIcon />
          </IconButton>
        </CardActions>
        <Collapse in={expanded} timeout="auto" unmountOnExit>
          <CardContent>
            <Typography className={classes.title} paragraph>
               :{" "}
            </Typography>
            <Typography className={classes.title} paragraph>
              {studentDetail?.address}
            </Typography>
            <Typography className={classes.title} paragraph>
                :{" "}
            </Typography>
            <Typography className={classes.title} paragraph>
              {studentDetail?.individualSkills}
            </Typography>
            <Typography className={classes.title} paragraph>
                :{" "}
            </Typography>
            <Typography className={classes.title} paragraph>
              {studentDetail?.primaryPhoneNumber +
                " - " +
                studentDetail?.secondaryPhoneNumber}
            </Typography>
          </CardContent>
        </Collapse>
      </Card>
    </Container>
  );
};
export default StudentDetail;
